  }
  .show-above-bpmed,
  .hide-below-bpmed {
    display: block;
  }
}

@media only screen and (min-width: 960px) {
  .hide-above-bplrg {
    display: none;
  }
  .show-above-bplrg,
  .hide-below-bplrg {
