/* ============================================ *
 * Grid styles
 * ============================================ */
.col-set {
  font-size: 0;
}

.col-set > * {
  outline: 1px solid #000;
  height: 50px;
  display: inline-block;
  vertical-align: top;
  margin-right: 2%;
  margin-bottom: 20px;
  width: 100%;
}

.col-set > *:last-child {
  margin-right: 0;
}

@media only screen and (min-width: 480px) {
  .col-set .col-4 {
    width: 49%;
  }
  .col-set .col-4:nth-of-type(2n) {
    margin-right: 0;
  }
}

@media only screen and (min-width: 600px) {
  .col-set .col-2 {
    width: 49%;
  }
  .col-set .col-3 {
    width: 32%;
  }
  .col-set .col-4 {
    width: 23.5%;
  }
  .col-set .col-4:nth-of-type(2n) {
    margin-right: 2%;
  }
  .col-set .col-4:last-child {
    margin-right: 0;
  }
  .col-set .col-5 {
    width: 18.4%;
  }
}

/* ============================================ *
 * Reset
 * ============================================ */
*,
*:before,
*:after {
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html {
  -webkit-tap-highlight-color: black(0);
  /* Prevent tap highlight on iOS/Android */
  -webkit-text-size-adjust: 100%;
  /* Prevent automatic scaling on iOS */
}

body {
  background: #FFFFFF;
  color: #000000;
  line-height: 1;
}

html,
body,
img,
fieldset,
abbr,
acronym {
  border: 0;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: 100%;
  font-weight: normal;
}

th,
code,
cite,
caption {
  font-weight: normal;
  font-style: normal;
  text-align: left;
}

address {
  font-style: normal;
}

fieldset {
  margin: 0;
  padding: 0;
  border: 0;
}

img {
  display: block;
}

ol,
ul {
  list-style: none;
}

q:before,
q:after {
  content: '';
}

input:focus,
input[type="search"]:focus {
  outline-offset: -2px;
}

input[type="search"] {
  -webkit-appearance: none;
  /* 1 */
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  /* 2 */
  box-sizing: border-box;
}

/* ============================================ *
 * Base Styles
 * ============================================ */
hr {
  border: 0;
  background-color: #A7A9AB;
  height: 1px;
  margin: 0 0 20px;
}

address {
  font-style: normal;
  line-height: 1.4;
}

img {
  display: inline-block;
  vertical-align: middle;
  max-width: 100%;
  height: auto;
  -ms-interpolation-mode: bicubic;
}

textarea {
  height: auto;
  min-height: 100px;
}

.clear {
  clear: both;
}

/* Vertical Centering
---------------------------------------------- */
.vwrapper {
  display: table;
  height: 100%;
  width: 100%;
}

.vcell {
  display: table-cell;
  vertical-align: middle;
}

/* ============================================ *
 * Buttons
 * ============================================ */
button {
  border: 0;
  background: #A7A9AB;
  padding: 4px 12px;
}

.button.search-button:after {
  display: none;
}

/* Primary Buttons */
.button {
  border-radius: 4px;
  background-color: #5c5c5c;
  display: inline-block;
  padding: 0 5px 0 10px;
  color: #ffffff;
  font-size: 14px;
  font-weight: 300;
  text-align: left;
  vertical-align: middle;
  position: relative;
  text-decoration: none;
  white-space: nowrap;
  text-decoration: none !important;
  height: 40px;
  line-height: 40px;
  cursor: pointer;
}

.button span {
  line-height: 40px;
  display: inline-block;
}

.no-touch .button:hover {
  background-color: #2b292a;
}

.button:after {
  font-family: "ufhs" !important;
  font-style: normal !important;
  font-weight: normal !important;
  font-variant: normal !important;
  text-transform: none !important;
  speak: none;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  vertical-align: middle;
  content: "i";
  display: inline-block;
  padding: 0 0 0 10px;
  font-size: 18px;
  line-height: 40px;
  color: #E60004;
}

.button:hover {
  background-color: #000000;
  color: #ffffff;
}

.unbutton {
  background: transparent;
  color: #58585B;
  border: none;
}

.no-touch .unbutton:hover {
  background-color: transparent;
}

/* Secondary Buttons */
.button.button-secondary {
  background: #DDDDDD;
  color: #58585B;
  border-color: #B8B8B8;
}

.no-touch .button.button-secondary:hover {
  background: #d0d0d0;
  cursor: pointer;
}

.button.button-secondary:active {
  background: #c4c4c4;
  color: #58585B;
}

.button.button-secondary:focus {
  color: #58585B;
  background: #c4c4c4;
  outline: none;
}

.button.button-secondary.arrow {
  padding: 6px 36px 4px 16px;
}

.button.button-secondary.arrow.back {
  padding: 6px 16px 4px 36px;
}

/* Disabled - class for anchor, state for form elements */
.button.disabled,
.button:disabled {
  background: #C8C8C8;
  border-color: shade(#C8C8C8, 20%);
  opacity: 0.6;
  cursor: not-allowed;
}

/* Adjacent buttons */
.button + .button {
  margin-left: 5px;
}

/* Buttons & anchor links */
.col-left .block .actions .button ~ a,
.col-right .block .actions .button ~ a {
  line-height: 33px;
  margin-right: 5px;
}

/* ============================================ *
 * CMS block buttons
 * ============================================ */
.m-button:before {
  font-family: "ufhs" !important;
  font-style: normal !important;
  font-weight: normal !important;
  font-variant: normal !important;
  text-transform: none !important;
  speak: none;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  vertical-align: middle;
  content: "n";
}

.m-button:after {
  font-family: "ufhs" !important;
  font-style: normal !important;
  font-weight: normal !important;
  font-variant: normal !important;
  text-transform: none !important;
  speak: none;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  vertical-align: middle;
  content: "E";
}

/* ============================================ *
 * FORMS - COMMON
 * ============================================ */
/* -------------------------------------------- *
 * Labels
 */
label {
  display: inline-block;
  font-size: 14px;
  font-weight: 300;
  -moz-user-select: none;
  -ms-user-select: none;
  -webkit-user-select: none;
  -o-user-select: none;
  user-select: none;
}

label.required:after,
span.required:after {
  content: ' *';
  color: #E60004;
  font-weight: normal;
  font-family: Open Sans, Verdana, Geneva, sans-serif;
  font-size: 12px;
}

label.required em,
span.required em {
  display: none;
}

/* -------------------------------------------- *
 * Textarea
 */
textarea {
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
  width: 100%;
  max-width: 450px;
  padding: 5px;
}

/* -------------------------------------------- *
 * Inputs
 */
.input-text {
  border: 1px solid #d2d2d2;
  background: #ffffff;
  font-size: 12px;
  font-weight: 300;
}

.input-text.validation-failed {
  border-color: #E60004;
}

.input-text.validation-failed:focus {
  outline-color: tint(#E60004, 50%);
}

input[type=email],
input[type=search],
input[type=number],
input[type=password],
input[type=tel],
input[type=text] {
  height: 30px;
  padding: 0 8px;
  width: 100%;
  max-width: 100%;
  -webkit-border-radius: 5px;
  border-radius: 5px;
  background-clip: padding-box;
  /* stops bg color from leaking outside the border: */
}

@media only screen and (min-width: 480px) {
  input[type=email],
  input[type=search],
  input[type=number],
  input[type=password],
  input[type=tel],
  input[type=text] {
    width: 365px;
  }
}

/* Only show on iPhones (not iPads or any other device) */
@media only screen and (max-device-width: 568px) and (-webkit-min-device-pixel-ratio: 0) {
  input[type=email],
  input[type=search],
  input[type=number],
  input[type=password],
  input[type=tel],
  input[type=text],
  select {
    font-size: 16px;
    /* Prevent from zooming on focus */
  }
}

/* -------------------------------------------- *
 * Inputs - Quantity
 */
input[type=number].qty,
input[type=text].qty {
  width: 3em;
  text-align: center;
  vertical-align: middle;
}

/* -------------------------------------------- *
 * Placeholder
 */
::-webkit-input-placeholder {
  color: #58585B;
}

input:-moz-placeholder {
  color: #58585B;
}

/* -------------------------------------------- *
 * Checkbox And Radio
 */
.checkbox,
.radio {
  position: relative;
  top: -1px;
  display: inline-block;
}

.checkbox + label,
.radio + label {
  width: auto;
  max-width: 85%;
  padding: 0 0 0 8px;
  font-size: 14px;
  font-weight: normal;
  vertical-align: top;
}

/* -------------------------------------------- *
 * Select
 */
select {
  height: 33px;
  width: 100%;
  max-width: 365px;
  -webkit-appearance: none;
  /*Removes default chrome and safari style*/
  appearance: none;
  /* Removes Default IE11 style*/
  text-indent: 0.01px;
  /* Removes default arrow from firefox*/
  text-overflow: '';
  /*Removes default arrow from firefox*/
  background: #ffffff url(data:image/png;base64,) no-repeat 100% 50%;
  background-position-x: 100%;
  background-position-y: center;
  border: 1px solid #A7A9AB;
  padding: 5px 30px 5px 14px;
  -moz-appearance: none;
  -webkit-border-radius: 8px;
  border-radius: 8px;
  background-clip: padding-box;
  /* stops bg color from leaking outside the border: */
}

select:hover {
  background: #ffffff url(data:image/png;base64,) no-repeat 100% 50%;
  background-position-x: 100%;
  background-position-y: center;
  border-color: #a2a4a6;
}

select::-ms-expand {
  /* Removes Default IE10 style*/
  display: none;
}

select:disabled {
  opacity: 0.8;
  filter: alpha(opacity=80);
}

select + select {
  margin-left: 5px;
}

select[multiple] {
  width: 270px;
  border: 1px solid #A7A9AB;
  font-size: 15px;
  padding: 5px;
}

.table, table.side, table.top-and-side {
  border-collapse: collapse;
  margin-bottom: 20px;
}

.table td, table.side td, table.top-and-side td,
.table th, table.side th, table.top-and-side th {
  padding: 8px 13px;
  text-align: left;
  border-left: 1px solid #A7A9AB;
  font-size: 14px;
}

.table th, table.side th, table.top-and-side th {
  color: #737679;
  border-bottom: 2px solid #737679;
}

.table td, table.side td, table.top-and-side td {
  border-top: 1px solid #A7A9AB;
}

.table tr th:first-of-type, table.side tr th:first-of-type, table.top-and-side tr th:first-of-type,
.table tr td:first-of-type, table.side tr td:first-of-type, table.top-and-side tr td:first-of-type {
  border-left: none;
}

.table .blank, table.side .blank, table.top-and-side .blank {
  border: none;
}

.table thead th, table.side thead th, table.top-and-side thead th {
  font-weight: bold;
  border-left: 1px solid #A7A9AB !important;
  border-right: 1px solid #A7A9AB;
}

.table thead th :first-of-type, table.side thead th :first-of-type, table.top-and-side thead th :first-of-type {
  border-left: none !important;
}

.table thead th :last-of-type, table.side thead th :last-of-type, table.top-and-side thead th :last-of-type {
  border-right: none;
}

.table thead th [colspan="2"], table.side thead th [colspan="2"], table.top-and-side thead th [colspan="2"],
.table thead th [colspan="3"], table.side thead th [colspan="3"], table.top-and-side thead th [colspan="3"],
.table thead th [colspan="4"], table.side thead th [colspan="4"], table.top-and-side thead th [colspan="4"],
.table thead th [colspan="5"], table.side thead th [colspan="5"], table.top-and-side thead th [colspan="5"] {
  text-align: center;
}

table.side th {
  text-align: right;
  border-right: 2px solid #737679;
  border-left: none;
  border-top: 1px solid #A7A9AB;
  border-bottom: none;
  vertical-align: middle;
}

table.side tr:first-of-type th,
table.side tr:first-of-type td {
  border-top: none;
}

table.top-and-side thead th {
  border-left: 1px solid #A7A9AB;
  border-right: none;
  border-top: none;
  border-bottom: 2px solid #737679;
}

table.top-and-side thead th :first-of-type {
  border-right: 2px solid #737679;
  border-left: none !important;
}

table.top-and-side tbody th {
  border-right: 2px solid #737679;
  border-bottom: 1px solid #A7A9AB;
}

table.top-and-side tbody tr:last-of-type th {
  border-bottom: 0px solid #fff;
}

